
@order_gray: #757575;
@img_edge: 80px;

@media (max-width:768px) {
    .order_center {
        .order_list_area {
            ul {
                li {
                    font-size: 8px;

                    img {
                        width: @img_edge*0.5 !important;
                        height: @img_edge*0.5 !important;
                    }
                }
            }
        }
    }
}

.order_center {
    // >h1 {
    //     color: @order_gray;
    //     font-size: 30px;
    //     font-weight: 400;
    //     margin: 0;
    // }

    .order_list_area {
        .single_order {
            // padding: 15px;
            margin-top: 20px;
            border-color: #ff6700;
            border-style: solid;
            border-width: 1px;
            position: relative;

            .order_info {
                padding: 30px 24px;
                background: #fffaf7;
                padding-bottom: 0px;
                border-bottom: 1px solid #feccac;
                position: relative;

                .right {
                    display: flex;
                    align-items: flex-end;

                    .order_total {
                        width: 100%;
                        text-align: right;

                        // p {
                        // }
                        span {
                            font-size: 28px;
                            color: #333;
                        }

                        // position: absolute;
                        // bottom: 24px;
                    }
                }

                .order_status {
                    font-size: 18px;
                    color: #ff6700;
                }

                p {

                    margin-bottom: 20px;
                    min-height: 28px;
                    box-sizing: content-box;


                    font-size: 14px;
                    color: rgb(117, 117, 117);

                    span.sep {
                        margin: 0 4px;
                    }
                }
            }

            .darr {
                padding: 20px;
                margin: 0;
                position: relative;

                .right {
                    display: flex;
                    justify-content: flex-end;

                    button {
                        display: block;
                        font-size: 12px;
                        width: 118px;
                        height: 28px;
                        margin-bottom: 15px;
                    }

                    .detail {
                        // position: absolute;
                        // right: 20px;
                        // top: 50px;
                        background-color: #fff;
                        color: #757575;
                        border: 1px solid #b0b0b0;

                        // transform: translateY(-50%);
                    }

                    .remove {
                        background-color: red;
                        color: white;
                        border: none;
                    }

                }

                ul {

                    margin: 0;
                    padding: 0;

                    li {
                        list-style: none;


                        img {
                            width: @img_edge;
                            height: @img_edge;
                        }

                        .info {
                            display: inline-block;
                            font-size: 14px;
                            color: rgb(51, 51, 51);
                            text-decoration: none;

                            a {
                                color: rgb(51, 51, 51);
                                text-decoration: none;
                                display: block;

                                &:hover {
                                    color: #ff6700;
                                }
                            }
                        }


                    }
                }
            }


        }

    }
}