
@title_black: #333;
@desc_gray: #b0b0b0;
@media (max-width:700px) {
    .panel {
        div.p_header {
            p {
                font-size: 16px;
                line-height: 1em;
            }

            div.right_tabs {
                // justify-content: left;

                a {
                    font-size: 10px;
                    margin-left: 5px;
                    line-height: 16px;
                }
            }
        }
    }


}

.panel {
    margin-top: 30px;

    .p_header {

        // display: flex;
        p {
            display: inline-block;
            font-size: 22px;
            font-weight: 200;
            line-height: 2rem;
        }

        .right_tabs {
            display: flex;
            justify-content: right;

            // float: right;
            a {
                // @media (max-width:700px) {
                //     font-size: 10px;
                //     margin-left: 10px;

                // }

                display: inline-block;
                // float: right;
                font-size: 16px;
                font-weight: 400;
                // line-height: 2rem;
                // padding: 0 10px;
                margin-left: 20px;
                text-decoration: none;
                color: black;
                transition: all .5s linear;

                &.a_selected {
                    color: orange !important;
                    border-bottom: 1px solid orange;
                    // text-decoration: underline;
                }

                >.fa-angle-right {
                    display: inline-block;
                    text-align: center;
                    color: white;
                    /* padding: 10px; */
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    background-color: var(--topbar_color);
                    font-size: 16px !important;
                    // line-height: 2rem;
                }
            }
        }

    }

    >.grid_container {
        margin-top: 10px;
        display: grid;
        justify-content: space-between;
        grid-template-columns: 18% 18% 18% 18% 18%;
        gap: 10px;
        /* background-color: #2196F3; */
        // padding: 10px;

        // p,
        // h6,
        // span,
        // del,
        // i {
        //     @media (max-width:700px) {

        //         font-size: 0.4rem !important;
        //     }
        // }

        .last_icon {
            height: 300px;
            text-align: center;
            font-size: 14px;
            text-decoration: none;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @media (max-width:700px) {
                height: 200px !important;
                font-size: 8px;
            }

            .last_icon_half {
                text-decoration: none;
                text-align: left;
                position: relative;
                width: 100%;
                height: 45%;
                background-color: white;
                transition: all .5s linear;
                color: @title_black;

                &:hover {
                    box-shadow: var(--shadow1);
                    transform: translateY(-1%);
                }

                img {
                    width: 45%;
                    display: inline-block;
                    float: right;
                }

                p {
                    margin-top: 5px;
                    padding: 10px;

                    @media (max-width:700px) {
                        padding: 0;
                        margin: 10px auto;

                    }
                }

                .price {
                    color: orange;
                }

                .fa-arrow-circle-o-right {
                    font-size: 60px;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 10%;
                    color: orange;
                    @media (max-width:700px) {
                        font-size: 15px;       
                    }
                }

                .more {
                    font-size: 18px;
                    @media (max-width:700px) {
                        font-size: 8px;
                    }
                    small {
                        font-size: 12px;
                        color: #757575;
                    }
                }
            }
        }

        .gitem {
            display: block;
            height: 300px;
            text-align: center;
            /* color:white; */
            /* width: 20%; */
            background-color: rgba(255, 255, 255, 0.8);
            text-align: center;
            /* padding: 60px; */
            /* max-width: 150px; */
            font-size: 30px;
            text-decoration: none;
            transition: all .5s linear;

            @media (max-width:700px) {
                height: 200px;
            }

            &:hover {
                box-shadow: var(--shadow1);
                transform: translateY(-1%);

            }

            >.gitem_card {
                width: 100%;
                height: 100%;
                color: black;
                position: relative;

                >h6 {
                    height: 45px;
                    font-size: 15px;
                    @media (max-width:700px) {
                        height: 30px;
                        font-size: 8px;
                    }
                }

                >img {
                    width: 60%;
                    display: block;
                    margin: 10px auto;
                }

                >p {
                    font-size: 12px;
                    color: @desc_gray;

                    @media (max-width:700px) {
                        font-size: 6px;

                    }
                }

                >.price {
                    box-sizing: border-box;
                    width: 100%;
                    text-align: center;
                    padding-bottom: 20px;
                    font-size: 15px;
                    position: absolute;
                    bottom: 10px;
                    /* left:50%; */
                    /* transform: translateX(-50%); */
                    color: orange;

                    >del {
                        color: var(--topbar_color);
                    }
                    @media (max-width:700px) {
                        font-size: 6px;
                    }
                }

            }

        }

        .left_banner {
            height: 610px;
            grid-row: 1 / span 2;

            >img {
                width: 100%;
                height: 100%;
            }

            @media (max-width:700px) {
                height: 410px;
            }
        }

        .half_left_banner {
            grid-row: span 1;
            height: 300px;

            @media (max-width:700px) {
                height: 200px;

            }
        }



    }
}
