
.collection_center {
    width: 100%;

    .collection_panel {
        ul {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            padding-left: 0;

            li {
                // flex-grow: 1;
                // float: left;
                display: block;
                width: 33%;
                // background-color: blueviolet;
                // height:348px;
                position: relative;
                margin-top: 30px;
                border-bottom: 1px solid #e0e0e0;

                a {
                    display: block;
                    text-decoration: none;
                    font-size: 14px;
                    color: #333;

                    &:hover {
                        color: #ff6700;
                    }
                }

                .price {
                    font-size: 14px;
                    color: #ff6700;
                }

                &:hover {
                    .bt_area {
                        button {
                            opacity: 1;
                        }
                    }
                }

                img {
                    width: 50%;
                    // height: 100px;
                    margin: 0 auto;
                    display: block;
                }

                p {
                    text-align: center;
                }

                .cname {
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }

                .bt_area {
                    display: flex;
                    justify-content: space-around;

                    button {
                        width: 35%;
                        // margin: 0 auto;
                        display: block;
                        opacity: 0;
                        transition: opacity .3s ease;
                        height: 28px;
                        width: 108px;
                        font-size: 12px;
                        transition: all .3s ease;

                        &.detail_bt {
                            color: white;
                            background-color: #ff6700;
                            border: none;
                        }

                        &.del_bt {
                            color: #b0b0b0;
                            border: solid 1px #b0b0b0;

                            &:hover {
                                background-color: #757575;
                                color: white
                            }
                        }
                    }
                }
            }
        }
    }
}