
@nav_gray: rgb(224, 224, 224);
@menu_min_height: 250px;
@nav_fs: 17px;
.showing {
    min-height: @menu_min_height !important;
    border-bottom: 1px solid @nav_gray;
    border-top: 1px solid @nav_gray;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, .5);
}

.mynavbar_outer {
    position: relative;

    .nav_menu {
        z-index: 400;
        // display: flex;
        // flex-wrap: wrap;
        // /* display: none; */
        // justify-content: space-between;
        // align-items: center;
        margin: 0;
        position: absolute;
        // left: 0px;
        top: 100%;
        width: 100%;
        // width: 100vw;
        height: 0px;
        min-height: 0;
        // min-height: @menu_min_height;
        overflow: hidden;
        background: white;
        transition: all .3s;
        transition-delay: .2s;


        // &:hover {
        //     min-height: @menu_min_height;
        // }

        .card {
            padding-bottom: 40px;
            border: none;
            border-radius: 0;
            width: 16.6%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            text-decoration: none;

            img {
                height: 100%;
                width: auto;
            }

            p {
                text-align: center;
                word-wrap: nowrap;
                font-size: 12px;
                margin-bottom: 2px;
            }

            .card_price {
                color: orange;
            }

            .card-img-top {
                margin-top: 25px;
                width: 100%;
                height: 0.5*@menu_min_height;
            }

            +.card {
                .card-img-top {
                    border-left: 1px solid @nav_gray;
                    border-radius: 0;
                }
            }
        }
    }

    .mynavbar {
        position: relative;
        margin: 0 auto;
        min-height:100px;
        .nav_items {
            height: 100%;
            display: flex;
            align-items: center;

            a {

                display: inline-block;
                /* padding: 10px 20px; */
                /* margin: 10px; */
                padding: 0 10px;
                text-decoration: none;
                font-size: @nav_fs;

                color: black;

                /* background-color: red; */
                &:hover {
                    color: orange;
                }

            }


        }


        .logo_div {
            display: block;
            img {
                max-height: 56px;
                &:active {
                    transform: scale(0.75,0.75);
                }
            }

            /* width: 100%; */

        }

        .search_box {
            height:100%;
            display: flex;
            align-items: center;
            input[type="text"] {
                width: 80%;
                float: left;
                display: inline-block;
                height:50px;
                border: solid 1px rgb(224, 224, 224);
            }

            button {
                width: 20%;
                display: inline-block;
                color:rgb(97, 97, 97);
                height:50px;
                float: left;
                border: solid 1px rgb(224, 224, 224);
                background-color: transparent;
                transition: all .4s ease;
                &:hover {
                    background-color: rgb(255,103,0);
                    color:white;
                }
            }
        }


    }
}


@media (max-width:900px) {
    .nav_menu .card {

        width: 33.3% !important;
        /* height: 50%; */
        height: 50% !important;
        border: none !important;
        padding-bottom: 5px !important;

        p {
            font-size: 8px !important;
        }

        .card-img-top {
            margin-top: 5px !important;
            height: 0.25*@menu_min_height !important;
            // width: auto;
        }
    }

    .mynavbar_outer {
        .mynavbar {
            .logo_div {
                img {
                    margin: 0 auto;
                }
            }

            .nav_items {
                a {
                    padding: 0 3px;
                    font-size: 0.8*@nav_fs;
                }
            }
        }
    }
}