
@left_ratio:16.67%;
@media (max-width:700px) {


  div.disp {
    .slide_menu {
      ul {
        li {
          a {
            span {
              font-size: 5px;
            }
          }
        }
      }
    }

    .left {
      font-size: 6px;

      ul {
        li {
          padding: 5px;
        }
      }
    }

    height: 250px;
  }

}

.disp {
  /* padding: 1px; */
  position: relative;
  /* overflow: auto; */
  width: 100%;
  height: 460px;

  .slide_menu {
    box-sizing: border-box;
    padding: 20px 0px;
    z-index: 100;
    position: absolute;
    // display: none;
    left: @left_ratio;
    top: 0%;
    width: 100%-@left_ratio;
    height: 100%;
    background-color: white;
    box-shadow: var(--shadow1);

    ul {
      width: 100%;
      height: 100%;
      list-style-type: none;
      display: flex;
      flex-wrap: wrap;
      align-content: space-around;

      li {
        width: 24%;
        height: 15%;
        /* float: left; */
        display: block;

        /* display: inline-block; */
        a {
          text-decoration: none;
          color: black;
          font-size: small;
          display: block;
          height: 100%;
          width: 100%;

          img {
            /* width:30px; */
            // height: 100%;
            width: 25%;
          }

          span {
            width: 75%;
          }
        }

      }
    }
  }

  .left {
    background-color: rgba(105, 105, 105, 0.6);
    box-sizing: border-box;
    /* overflow: hidden; */
    height: 100%;
    float: left;
    padding: 20px 0px;
    width: @left_ratio;
    margin: 0;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 20;
    font-size: 15px;




    ul {
      list-style-type: none;
      margin: 0;
      padding-left: 0px;

      li {
        color: #fff;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-right: 10px;
        padding-left: 10px;
        height: 15%;

        i {
          float: right;
        }
      }

    }

  }

  .right {
    display: block;
    float: left;
    width: 100%;
    height: 100%;
    z-index: 10;
    /* background-color: red; */
    overflow-wrap: break-word;
    position: relative;

    .mySwiper {
      width: 100%;
      height: 100%;
      background-color: transparent;
      --swiper-navigation-color: rgb(124, 124, 124);
      --swiper-pagination-color: rgb(124, 124, 124);

      .swiper-button-prev {
        left: @left_ratio;
        /* top: 0; */
        position: absolute;
      }

      .swiper-pagination {
        position: absolute;
        left: @left_ratio;
        width: 80%;
      }

      .swiper-slide {
        img {
          width: 100%;
        }

        a img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }



    }

  }



  .left ul li:hover {
    background-color: rgb(255, 106, 0);
    cursor: pointer;
  }

  .left ul li:hover .slide_menu {
    display: block;
  }



  .left .slide_menu ul li a:hover {
    color: orange;
  }


}
