
@icon_radius: 12px;
@black42: #424242;

.part1 {
    .list {
        background-color: white;
        margin-top: 38px;
        padding: 0;
        color: @black42;

        .list_header {
            height: 70px;
            width: 100%;

            @media (max-width:576px) {
                display: none !important;
            }
        }

        .list_row {
            border-bottom: solid 1px #e0e0e0;
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            .list_col {
                width: 10%;
                // border-right: solid 1px red;
                font-size: 14px;
                text-align: left;
                padding-left: 10px;
                text-align: center;

                .iconfont {
                    display: inline-block;
                    width: 19px;
                    height: 19px;
                }

            }

            .price_count {
                color: #ff6700;
            }

            .name_col {
                width: 40%;
                text-align: left;

                a {
                    text-decoration: none;
                    color: @black42;
                }
            }

            .img_col {
                @media (max-width:576px) {
                    width: 50%;
                }

                img {
                    width: 60%;
                }
            }

            .quantity_col {
                .quantity_wrap {
                    display: flex;
                    border: solid 1px rgb(224, 224, 224);
                    box-sizing: content-box;
                    height: 38px;

                    // padding: 1px;
                    * {
                        border: none !important;
                        padding: 0;
                    }

                    button {
                        width: 25%;
                        height: 100%;
                        color: #757575;
                        background-color: white;
                        transition: background-color .3s ease;

                        &:hover {
                            background-color: #e0e0e0;
                        }
                    }

                    input {
                        text-align: center;
                        // margin-left: -20px;
                        width: 50%;
                        padding: 0;
                        height: 100%;
                        outline: none;
                    }
                }
            }

            .rm_col {
                a {
                    margin: 0 auto;
                    width: 24px;
                    height: 24px;
                    display: block;
                    text-align: center;
                    color: #757575;
                    transition: background-color .2s ease;

                    &:hover {
                        background-color: rgb(229, 57, 53);
                        border-top-left-radius: @icon_radius;
                        border-top-right-radius: @icon_radius;
                        border-bottom-left-radius: @icon_radius;
                        border-bottom-right-radius: @icon_radius;
                        color: white;
                        // i {
                        //     color:white;
                        // }
                    }
                }
            }

            .single_price_col,
            .quantity_col,
            .price_count,
            .rm_col {
                @media (max-width:576px) {
                    width: 25%;
                }
            }
        }

        .item_row {
            box-sizing: content-box;
            min-height: 85px;
            padding: 15px 0;
        }
    }

    .cart_calc_action {
        min-height: 50px;
        background-color: white;
        align-items: center;
        width: 100%;
        margin: 0 auto;

        margin-top: 30px;

        .left {
            color: #757575;
            font-size: 14px;

            a {
                color: #757575;
                text-decoration: none;
                margin-left: 32px;
            }

            span {
                margin-left: 16px;
                padding-left: 16px;
                border-left: 1px solid #eee;

                i {
                    font-style: normal;
                    color: #ff6700;
                }
            }
        }

        .right {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            height: 100%;

            // padding-right: 0;
            // flex-wrap: wrap;
            span {
                margin-right: 50px;
                color: #ff6700;

                em {
                    font-size: 30px;
                    font-style: normal;
                }
            }

            button {
                width: 40%;
                height: 100%;
                display: inline-block;
                border: none;
                font-size: 18px;
                background-color: #e0e0e0;
                color: #b0b0b0;
                height: 50px;


            }

            .button_active {
                background-color: #ff6700;
                color: white;

                &:hover {
                    background-color: #f25807;
                }
            }
        }

    }
}