
@Mygray: #757575;
@myorange: #ff6700;

.cct_outer {
    border-bottom: solid 2px @myorange;
    background-color: white;

    .cct {
        display: flex;
        align-items: center;
        min-height: 100px;
        flex-wrap: wrap;

        .logo_div {
            img {
                max-height: 56px;
                &:active {
                    transform: scale(0.75,0.75);
                }
            }
        }

        h2 {
            padding-left: 50px;
            color: #424242;
            font-size: 28px;
        }

        p {
            font-size: 12px;
            line-height: 34px;
            margin: 0 15px 0;
            color: @Mygray
        }

        .right {
            flex-grow: 1;
            display: flex;
            justify-content: flex-end;
            font-size: 12px;

            span {
                color: rgb(176, 176, 176);
            }

            a {
                color: @Mygray;
                text-decoration: none;
                padding: 0 15px;

                &:hover {
                    color: @myorange
                }
            }
        }
    }

}