
.part2 {
    box-sizing: content-box;
    background-color: rgb(245, 245, 245);
    padding-top: 40px;
    padding-bottom: 40px;

    .checkout {
        background-color: white;
        padding-top: 48px;

        .addr_header {
            margin-bottom: 20px;
        }

        .addr_list {
            .addr_item {
                text-decoration: none;
                margin-bottom: 30px;
                display: block;
                height: 180px;
                position: relative;
                max-width: 270px;
                border: solid 1px rgb(224, 224, 224);
                cursor: pointer;

                &:focus {
                    border-color: #ff6700;
                }

                color:#757575;
                font-size: 14px;

                &:hover {
                    border-color: #b0b0b0;
                }

                .addr_info {
                    padding: 15px 24px 0;

                    .name {
                        font-size: 18px;
                        color: #333333;
                    }
                }

                .addr_action {
                    color: #ff6700;
                    font-size: 16px;
                    position: absolute;
                    right: 24px;
                    bottom: 20px;
                }
            }
        }

        .detail_sec {
            border-bottom: solid 1px #e0e0e0;

            .detail_header {
                font-size: 18px;
                color: #333;
                height: 40px;
            }

            .item {
                // display: flex;
                min-height: 30px;
                padding: 15px 0;

                .item_img {
                    display: inline-block;

                    img {
                        max-width: 50px;
                    }
                }

                .item_name {
                    display: inline-block;
                    font-size: 14px;
                    color: rgb(66, 66, 66);
                }

                .item_price {
                    float: left;
                    font-size: 14px;
                    color: rgb(66, 66, 66);
                }

                .count {
                    float: right;
                    font-size: 14px;

                    color: #ff6700;
                }
            }
        }

        .delivery {
            padding: 25px 0;
            border-bottom: solid 1px #e0e0e0;

            // margin: 0 50px;
            span {
                height: 40px;
                padding-right: 40px;
            }

            .way {
                font-size: 18px;
                color: #333;
            }

            .wayv {
                color: #ff6700;
                font-size: 14px;
            }
        }

        .summary {
            min-height: 250px;
            padding: 20px 0;
            margin: 0 50px;

            position: relative;
            border-bottom: solid 1px #e0e0e0;

            .sum_table {
                position: absolute;
                right: 0;
                bottom: 20px;

                tbody {
                    tr {
                        td {
                            text-align: right;
                            padding: 10px;
                            font-size: 14px;
                            // border: solid 1px black;

                        }

                        .key {
                            color: #757575;
                        }

                        .value {
                            color: #ff6700;

                            span {
                                font-size: 30px;
                            }
                        }
                    }
                }
            }
        }

        .foot {
            padding: 20px 50px;
            display: flex;
            justify-content: flex-end;

            button {
                width: 158px;
                height: 38px;
                font-size: 14px;
            }

            .back {
                color: #b0b0b0;
                border: solid 1px #b0b0b0;
            }

            .go {
                margin-left: 30px;
                background-color: #ff6700;
                border: none;
                color: white;
            }
        }
    }
}