
.clearfix::after {
    content: " ";
    display: table;
}

.outer {
    background-color: #f5f5f5;
    padding: 38px 0;

    .body {
        background-color: #fff;

        .notification {
            padding: 30px 48px;
            padding-left: 183px;
            position: relative;
            display: block;

            .order_info {
                padding: 20px 0;
                height: auto;

                .left {
                    float: left;

                    h2 {
                        font-size: 24px;
                        font-weight: 400;
                        line-height: 36px;
                        margin-bottom: 10px;
                        color: rgb(66, 66, 66);
                    }

                    p {
                        color: rgb(97, 97, 97);
                        font-size: 14px;
                    }
                }

                .right {
                    float: right;

                    .total {
                        font-size: 14px;
                        margin-bottom: 10px;

                        span {
                            color: #ff6700;

                            em {
                                font-style: normal;
                                font-size: 24px;
                            }
                        }

                    }

                    a.order_detail_check {
                        display: block;
                        text-align: right;
                        text-decoration: none;
                        font-size: 14px;
                        color: #757575;

                        &:hover {
                            color: #ff6700;
                        }

                    }
                }
            }

            i.hook {
                position: absolute;
                left: 50px;
                top: 40px;
                font-size: 80px;
                border-radius: 42px;
                border: 2px solid #83c44e;
                color: #83c44e;
            }

            

            .details {
                height: 180px;
                overflow-y:scroll;
                transition: height .3s ease;
                box-sizing: border-box;
                border-top: 1px solid #e0e0e0;
                padding: 20px 0 0 0;
                margin-top: 10px;
                font-size: 14px;

                ul {
                    list-style: none;

                    li {
                        line-height: 24px;

                        label {
                            width: 85px;
                            float: left;
                        }
                        .content {
                            float: left;
                            span {
                                display: block;
                            }
                        }
                    }
                }
            }
            .myhidden {
                height: 0 !important;
                border: none !important;
                overflow: hidden ;
            }
        }
    }
}