
@media (max-width:700px) {
  .side_panel {
    display: none;
  }
}
.side_panel {
    z-index: 400;
    position: fixed;
    right: 0;
    top: 50%;
    transform: translateY(-50%);



    a {
        text-decoration: none;
        color: var(--topbar_color);
        display: block;
        padding: 10px 10px;
        background-color: white;
        font-size: 14px;
        text-align: center;
        transition: all .5s linear;

        img {
            width: 25px;
            height: 25px;
            /* padding: 10px auto; */
            margin: 0px auto;
            display: block;
        }

        .Bhover {
            display: none;
        }

        &:hover {
            color: orange;

            .Bhover {
                display: block;
            }

            .static {
                display: none;
            }
        }
    }





    .download_qr1 {
        position: relative;

        .qrCode {
            font-size: 10px;
            position: absolute;
            left: 0;
            top: 0;
            margin-left: -130px;
            width: 0px;
            height: 120px;
            background-color: white;
            overflow: hidden;
            transition: .5s;
            color: var(--topbar_color);

            /* display: none; */
            img {
                width: 80px;
                height: 80px;
                margin: 10px auto;
            }

            .qrTriangle {
                position: absolute;
                left: 100%;
                top: 50%;
                margin-top: -26px;
                border-left: 6px white solid;
                border-right: 6px transparent solid;
                border-top: 6px transparent solid;
                border-bottom: 6px transparent solid;

                color: blueviolet;
                font-size: 15px;
            }
        }


        &:hover {
            .qrCode {
                width: 120px;
                overflow: visible;
            }
        }
    }




}
