
.mycards {
    margin-top: 60px;

    .card {
        position: relative;
        height: 300px;
        margin-bottom: 15px;
        border-radius: 0px;
        border: none;
        overflow: hidden;

        @media (max-width: 768px) {
            height: 200px;

            * {
                font-size: 8px !important;
            }

            img {
                width: 50% !important;
            }
        }

        .gocheck {
            position: absolute;
            transition: all .3s ease;
            bottom: -28px;
            left: 50%;
            transform: translateX(-50%);
            font-size: 12px;
            width: 120px;
            height: 28px;
            border: solid 1px #ff6700;
            color: #ff6700;
            background-color: white;

            &:hover {
                color: white;
                border-color: #f25807;
                background-color: #f25807;
            }
        }

        &:hover {
            .gocheck {

                bottom: 20px;
            }


        }

        .card-title {
            font-size: 14px;
            height: 18px;
            text-align: center;
            margin-bottom: 10px;
            color: #333;
        }

        .card-text {
            text-align: center;
            font-size: 14px;
            color: #ff6700;
        }
    }
}

.rec_title {
    margin-top: 80px;
    border-top: solid 1px rgb(224, 224, 224);
    position: relative;

    span {
        position: absolute;
        left: 50%;
        top: 0;
        display: block;
        padding: 0 40px;
        white-space: nowrap;
        transform: translate(-50%, -50%);
        background-color: rgb(245, 245, 245);
        color: rgb(117, 117, 117);
        font-size: 30px;
    }
}