
.outer {
    background-color: rgb(245, 245, 245);
    padding: 38px 0;

    .cart_success {
        padding-bottom: 25px;
        margin-bottom: 25px;

        .left {
            display: flex;

            img {
                width: 64px;
                height: 64px;
                display: block;

            }

            .success_info {
                margin-left: 20px;
            }
        }

        .right {
            .bt_area {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                button {
                    width: 40%;
                    height: 40px;
                    font-size: 14px;
                    transition: all .3s ease;
                }
                .back {
                    background:none;
                    border: solid 1px #b0b0b0;
                    color: #b0b0b0;
                    &:hover {
                        background-color: white;
                        color:#757575;
                    }
                }
                .gopay {
                    background-color: #ff6700;
                    color:white;
                    border:none;
                    &:hover {
                        background-color: #f25807;
                    }

                }

            }
        }
    }

}
