
@header_a_gray: rgb(97, 97, 97);
@swiper_gray: rgb(124, 124, 124);
@info_p_gray: rgb(176, 176, 176);
@choice_gray: rgb(224, 224, 224);
@summary_gray: rgb(249, 249, 250);
@my_orange: rgb(255, 103, 0);

.outertest {
    width: 200px;

    .test {
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }

}

.product_header {
    box-shadow: 0px 5px 5px rgba(0, 0, 0, .07);

    .row {
        display: flex;
        align-content: center;

        h2 {
            font-size: 18px;
            font-weight: 400;
            // line-height: 60px;
        }

        ul {
            list-style-type: none;
            display: flex;
            justify-content: right;

            li {
                // float:right;
                display: inline-block;

                // padding: 0 3px;
                span {
                    padding: 0 6px;
                }

                a {
                    text-decoration: none;
                    cursor: pointer;

                    color: @header_a_gray;
                    // font-size:16px;
                }
            }
        }
    }
}

.details {
    .row {
        .mySwiper {
            --swiper-navigation-color: @swiper_gray;
            --swiper-pagination-color: @swiper_gray;

            a {
                display: block;
            }

            img {
                height: auto;
                // max-height: 560px;
                width: 100%;
                display: block;
                // width:auto;
            }

            // max-height: 560px;

        }

        .info {
            // display: flex;
            // flex-direction: column;
            // justify-content: space-between;

            h2 {
                font-size: 24px;
                font-weight: 400;
            }

            p {
                font-size: 14px;
            }

            .info_p_gray {
                color: @info_p_gray;
            }

            .orange {
                color: @my_orange !important;
            }

            .orange_border {
                border: 1px @my_orange solid !important;
            }

            .selected {
                .orange();
                .orange_border();
            }

            .price_info {
                padding: 12px 0 10px;
                font-size: 18px;

                del {
                    font-size: 14px;
                    margin-left: 5px;
                    color: @info_p_gray;
                }
            }



            .summary {
                padding: 30px;
                background-color: @summary_gray;
                margin-bottom: 30px;
                font-size: 14px;
                color: @header_a_gray;

                p {
                    padding-top: 20px;
                    font-size: 24px;
                }

                del {
                    margin-left: 5px;
                }
            }

            .sale_btn {

                margin-bottom: 20px;

                button {
                    width: 100%;
                    position: relative;
                    height: 52px;
                    line-height: 52px;
                    color: white;
                    border: none;
                    transition: background-color .2s linear;
                    margin-bottom: 5px;

                }


                .cart_btn {
                    // width: 298px;
                    background-color: @my_orange;
                    margin-right: 10px;
                }

                .cart_btn:hover {
                    background-color: #f25807;
                }

                .like_btn {
                    // width: 140px;
                    background-color: #b0b0b0;

                    &.liked {
                        i {
                            color: #e53935;

                            .shadow {
                                display: block;
                                position: absolute;
                                top: 0;
                                left: 0;
                                animation: heartglow 1s ease;
                            }

                            @keyframes heartglow {
                                0% {
                                    -webkit-transform: scale(1) translate(0);
                                    transform: scale(1) translate(0);
                                    opacity: 1;
                                }

                                10% {
                                    -webkit-transform: scale(1) translate(-30%, -30%);
                                    transform: scale(1) translate(-30%, -30%);
                                    opacity: .7;
                                }

                                100% {
                                    -webkit-transform: scale(3) translate(-.5%, -.5%);
                                    transform: scale(3) translate(-.5%, -.5%);
                                    opacity: 0;
                                }
                            }
                        }
                    }

                    i {
                        margin-right: 5px;
                        position: relative;

                        .shadow {
                            display: none;
                        }
                    }
                }

                .like_btn:hover {
                    background-color: #757575;
                }
            }

            .guarantees {
                ul {
                    list-style-type: none;
                    margin: 0;
                    padding: 0;

                    li {
                        display: inline-block;
                        color: @info_p_gray;
                        margin-right: 15px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}
