
@mytopbar_fs: 12px;
@cart_height: 100px;
@cart_width: 316px;
@cart_font_color: rgb(66, 66, 66);
@cart_font_size: 12px;
@cart_item_border_color: rgb(224, 224, 224);
@myorange: rgb(255, 103, 0);
@list_length: 450px;

.mycart {
    /* float: right; */
    /* margin-left: 30px; */
    // color: #fff;
    position: relative;
    padding: 0 !important;
    background-color: @myorange;

    &:hover {
        .cart_box {
            color: @myorange;
            background-color: white;

        }
    }

    /* display: flex; */
    .cart_box {
        padding: 0 10px;
        // width:120px;
        // text-align: center;
        cursor: pointer;
        display: block;
        /* align-items: center; */
        margin: auto 0;
        /* height: 100%; */
        font-size: @mytopbar_fs;
        color: white;

        &:hover {
            +.cart_content {
                // min-height: @cart_height;
                // min-height: @cart_height;
                height: @cart_height;
            }
        }
    }

    .cart_content {
        padding: 0;
        margin: 0;
        color: @cart_font_color;
        font-size: @cart_font_size;

        &:hover {
            // min-height: @cart_height;
            height: @cart_height;
        }

        .mySpinner {
            color: @myorange;
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .cart_list {
            position: relative;

            ul {
                max-height: @list_length;
                overflow-y: scroll;
                padding: 0;
                width: 100%;
                margin: 0;

                li {
                    height: @cart_height;
                    display: flex;
                    padding: 0;
                    margin: 0;
                    position: relative;
                    list-style-type: none;
                    width: 90%;
                    margin: 0 auto;
                    height: @cart_height;

                    border-top: solid 1px @cart_item_border_color;
                    align-items: center;

                    &:hover {
                        .cart_item_price {
                            span {
                                opacity: 100;
                                color: @cart_font_color
                            }
                        }
                    }

                    img {
                        width: 50px;
                        height: auto;
                    }

                    p {
                        padding: 0;
                        margin: 0;
                    }

                    a {
                        text-decoration: none;

                        .cart_item_name {
                            width: 100px;
                            line-height: 12px;
                            word-wrap: break-word;
                            color: @cart_font_color;

                            &:hover {
                                color: @myorange !important;
                            }
                        }
                    }

                    .cart_item_price {
                        position: absolute;
                        right: 0px;
                        top: 50%;
                        transform: translateY(-50%);

                        span {
                            padding-left: 10px;
                            opacity: 0;
                            font-size: 16px;
                            color: rgb(176, 176, 176);
                            // background-color: red;
                            transition: opacity .5s ease;

                            &:hover {
                                cursor: pointer;
                            }
                        }
                    }
                }
            }

            .cart_action {
                display: flex;
                justify-content: space-around;
                align-items: center;

                .side {
                    width: 40%;
                    height: 60%;
                    // background-color: red;
                    text-align: left;

                    p {
                        margin: 0;
                    }

                    .number {
                        color: #757575;
                        font-size: @cart_font_size;
                        line-height: @cart_font_size;
                    }

                    .price {
                        color: @myorange;
                        font-size: 2*@cart_font_size;
                        line-height: 2*@cart_font_size;
                    }

                    a {
                        display: block;

                        button {
                            background-color: @myorange;
                            color: white;
                            height: 100%;
                            width: 100%;
                            border: none;
                        }
                    }
                }

                width: @cart_width;
                height: 70px;
                // background-color: aqua;
                position: absolute;
                bottom: -70px;
            }
        }

        .unlogged,.no_item {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }


        background-color: #fff;
        z-index: 1000;
        /* height: 0px; */
        box-shadow: var(--shadow1);
        position: absolute;
        /* padding: 10px 20px; */
        /* min-width: 150px; */
        transition: all .4s ease;
        transition-delay: .1s;
        right: 0;
        width: @cart_width;
        // min-height: 200px;
        // max-height: 500px;
        height:0;

        // min-height: 0;
        overflow:hidden;


    }

}
