
@len1: 16px;
@len2: 30px;
@my_orange: rgb(255, 103, 0);

#myModal {



    position: relative;

    .loading-enter,
    .loading-leave-to {
        transform: translate(-50%, -200%) !important;
        opacity: 0;
    }

    .loading-enter-to,
    .loading-leave {
        opacity: 1;
        transform: translate(-50%, -50%) !important;
    }

    .loading-enter-active,
    .loading-leave-active {
        // transform: translateY(0%);
        transition: all 1s ease;

    }


    .disp_status-enter,
    .disp_status-leave-to {
        transform: translate(-50%, -200%) !important;
        opacity: 0;
    }

    .disp_status-enter-to,
    .disp_status-leave {
        opacity: 1;
        transform: translate(-50%, -50%) !important;
    }

    .disp_status-enter-active,
    .disp_status-leave-active {
        // transform: translateY(0%);
        transition: all 1s ease;

    }

    .surface {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .my_spin {
        // width:50%;
        // height: auto;
        z-index: 1000;
        --bs-spinner-width: 50px;
        --bs-spinner-height: 50px;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        // border-color: @my_orange;
        color: @my_orange;
        // margin: auto;
        // position: absolute;
        // left: 50%;
        // top:50%;
        // transform: translate(-50%,-50%);
    }

    .disp_status {
        width: 120px;
        height: 80px;
        position: absolute;
        top: 50%;
        left: 50%;
        // font-size: 50px;
        text-align: center;

        transform: translate(-50%, -50%);
        display: block;
        z-index: 1010;

        i {
            font-size: 40px;
        }
    }

    .loading {
        // width: 100%;
        // height: 100%;
        opacity: .8;
        background-color: white;
        // display: flex;
        // justify-content: center;
        // align-items: center;
        z-index: 1000;

    }

    .alert {
        width: 100%;
        font-size: 10px;
    }

    .modal-header {
        .mheader {
            img {
                top: @len1;
                left: @len1;
                width: @len2;
                height: auto;
                position: absolute;
            }

            >button {
                text-align: center;
                position: absolute;
                top: @len1;
                right: @len1;
                line-height: @len2;
                width: @len2;
                height: @len2;
                padding: 0;
                // padding: 5px 10px;
                // padding: 10px;
                // font-size: 17px;
                // cursor: pointer;
            }

        }

    }

    p {
        word-wrap: unset;
        //overflow-wrap: unset;
        line-height: 32px;

    }

    .input-group {
        position: relative;

        .myFocus {
            width: 80%;

        }

        input {
            flex: none !important;
            width: 90px;
            font-size: 12px;
            transition: width .4s ease-in-out;
            height: 45px;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            &:focus {
                .myFocus();
            }

        }

        .input-group-text {
            display: inline-block;
            text-align: center;
            width: 20%;
        }

        .vis {
            display: block;
            position: absolute;
            right: 0;
            z-index: 500;
            text-align: center;
            font-size: 16px;
            line-height: 38px;
            height: 45px;
            padding: 0;
            border: none;
            background-color: transparent;
            border-radius: 5px;

            &:hover {
                background-color: rgba(4, 170, 109, .3);
            }
        }

        .green_vis {
            background-color: rgb(4, 170, 109);
            color: white;
        }

    }

    .modal-footer {
        button {
            z-index: 100;
        }
    }
}

@media (max-width:576px) {
    #myModal {
        .row {
            p {
                font-size: 12px;
            }
        }
    }
}
