
@img_edge: 60px;
@person_black: rgb(51, 51, 51);
@label_size: 17px;
@notice_orange: rgb(255, 105, 0);

.person_center {
    form {
        .info_item {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            margin-top: 30px;
            // background-color: green;
            min-height: @img_edge;

            >label {
                padding-left: 30px;
                width: 100px;
                display: block;
                // line-height: @img_edge;
                color: @person_black;
                font-size: @label_size;

                @media (max-width:576px) {
                    width: 100%;
                    margin-bottom: 10px;
                }
            }

            .info_content {
                flex-grow: 1;
                padding-left: 30px;
                position: relative;
                .choice {
                    label {
                        padding-left: 5px;
                        padding-right: 20px;
                    }
                }
                &.avatar {
                    cursor: pointer;
                }

                >.fa {
                    position: absolute;
                    right: 10px;
                    top: 50%;
                    transform: translateY(-50%);
                    font-size: 20px;
                }

                img {
                    width: @img_edge;
                    height: @img_edge;
                }

                p.notice {
                    margin: 0;
                    color: @notice_orange;
                }

                p.constant {
                    margin: 0;
                    color: @person_black;
                }

                button.edit {
                    width: 100%;
                    background-color: @notice_orange;
                    border: none;
                }
            }

        }
    }
}
