
.product_area {

    .home-banner-box {
        margin: 30px 0;
        width: 100%;

        img {
            width: 100%;
        }
    }

    .panel {
        .video_container {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            width: 100%;
            margin-top: 20px;
            padding-bottom: 50px;
            margin-bottom: 0px;

            .video_img {
                min-height: 200px;
                width: 20%;
                background-color: white;
                text-decoration: none;
                color: black;
                display: block;
                position: relative;
                transition: all .5s linear;

                @media (max-width:700px) {
                    width: 40%
                }

                .fa-play {

                    padding: 5px;
                    top: 50%;
                    left: 10%;
                    position: absolute;
                    color: white;
                    display: inline-block;
                    border-radius: 50%;
                    border: 2px solid white;
                }

                img {
                    width: 100%;
                    height: 70%;

                    &:hover~.fa-play {
                        background-color: orange;
                    }

                }

                &:hover {
                    box-shadow: var(--shadow1);
                    transform: translateY(-1%);
                }
            }
        }
    }


    background-color: rgb(245, 245, 245);
}









