
.post_area {
    width: 100%;
    background-color: var(--gray_bg);
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;

    .xiaomi_logo {
        /* width:10%; */
        display: block;
        height: auto;
        width: 10%;

        /* max-height: 100px; */
        /* background-color: aqua; */
        img {
            /* width:60px; */
            /* height:30px; */

            padding-top: 30px;
            width: 100%;
            max-width: 60px;
            max-height: 60px;
        }

    }

    .post_right {
        font-size: 13px;
        padding: 20px;
        color: var(--topbar_color);
        box-sizing: border-box;
        width: 90%;
        display: inline-block;

        /* background-color: blueviolet; */
        img {
            /* width:60px; */
            /* height:30px; */

            max-width: 60px;
            max-height: 60px;
        }
    }


    .slogan {
        width: 100%;
        text-align: center;
        color: var(--topbar_color);
    }

}
