
#avatarModal {
    @a_size: 18px;

    #reupd_bt {
        visibility: hidden;
        position: absolute;
    }

    .modal-header {
        position: relative;
        border: none !important;

        button {
            i.fa {
                font-size: 25px;
            }

            position: absolute;
            background-color: transparent;
            right:10px;
            top:20px;
            display: block;
            border: none;
        }
    }

    .modal-body {
        border: none;

        .myTitle {
            text-align: center;
            font-size: @a_size;
            font-weight: 500;
            color: rgb(51, 51, 51);
        }

        .upAvatar {
            position: relative;
            width: 80%;
            height: 80px;
            // margin-top: 30px;
            margin: 0 auto;
            margin-top: 50px;
            margin-bottom: 30px;

            button {
                background-color: rgb(255, 105, 0);
                width: 100%;
                height: 100%;
                display: block;
                font-size: @a_size;
                text-align: center;
                color: rgb(249, 249, 249);
                line-height: 80px;
                border-color: transparent;
                border-radius: 10px;
            }

            .inp_f_avatar {
                opacity: 0;
                position: absolute;
            }

        }

        .notice {
            text-align: center;
        }

        .alterAvatar {
            img {
                width: 360px;
                display: block;
                margin: 20px auto;
                height: 360px;
            }
        }

        .alterBt {
            width: 240px;
            margin: 20px auto;
            display: flex;
            justify-content: space-between;

            button {
                width: 45%;
                height: 60px;
            }
        }
    }

    .modal-footer {
        border: none;
    }

}
