  padding: 0;
}

:root {
  --topbar_color: rgb(176, 176, 176);
  --shadow1: 0px 8px 16px 0px rgba(0, 0, 0, 0.5);
  --gray_bg: rgb(245, 245, 245);
  --dark_bg: rgb(51, 51, 51);
}

#app {
  height: 100%;
  width: 100%;
  // overflow-x: hidden;
}




.clear_fix::after {
  content: "";
  clear: both;
  display: table;
  margin: 20px 0;
}






