
.bottom_area {
  background-color: white;

  .service {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px var(--topbar_color) solid;
    flex-wrap: wrap;

    .service_box {
      text-align: center;
      width: 20%;
      font-size: 25px;

      /* padding: 30px 0; */
      /* height:60%; */
      /* background-color: aquamarine; */
      +.service_box {
        border-left: 2px var(--topbar_color) solid;
      }
    }
  }

  .links {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    height: 460px;

    /* margin-block-end: 0; */
    dl {
      /* padding: 5px 0; */
      /* background-color: aquamarine; */
      display: block;
      width: 13.33%;
      height: 100%;
      list-style-type: none;
      margin-bottom: 0;

      /* margin-block-end: 0; */
      dt {
        padding: 30px 0;
      }

      dd {
        font-size: 12px;
        padding: 10px 0;
      }

      dd>a {
        text-decoration: none;
        color: var(--topbar_color);
      }
    }

    .contact {
      text-align: center;
      width: 20%;
      height: 150px;
      /* background-color: aqua; */
      border-left: 2px solid var(--topbar_color);

      .telephone {
        color: orange;
        font-size: 27px;
      }

      p {
        margin-top: 15px;
        color: var(--topbar_color);
      }

      a {
        display: inline-block;
        text-decoration: none;
        color: orange;
        margin-top: 10px;
        padding: 5px 30px;
        border: 2px solid orange;
      }
    }


  }
}

@media (max-width:700px) {
  .bottom_area {

    div.service {
      div.service_box {
        width: 50%;
        font-size: 15px !important;
        border: none !important;
      }
    }
    .links {

      dl {
        display: block;
        width: 50%;
        height: 25%;
  
        dt {
          font-size: 8px !important;
          padding: 5px 10px;
        }
    
        dd {
          /* font-size: 7px; */
          text-align: center;
            line-height: 8px;
            padding: 2px 0px;
            margin-bottom: 3px;
            a {
              
              font-size: 6px !important;
            }
        }
      }
  
  
      .contact {
        text-align: center;
        width: 100%;
        height: 25%;
        /* height: 150px; */
        /* background-color: aqua; */
        /* border-left: 2px solid var(--topbar_color); */
        border: none;
        margin-top: 3px;
        .telephone {
          /* color: orange; */
          font-size: 17px !important;
          margin-top: 3px;
        }
    
        p {
          /* margin-top: 10px; */
          color: var(--topbar_color);
          margin: 0;
        }
    
        a {
          display: inline-block;
          text-decoration: none;
          color: orange;
          margin-top: 10px;
          padding: 5px 10px;
          border: 2px solid orange;
          font-size: 10px !important;
        }
      }
  
    }

  }

}
