
@media (max-width:1280px) {
  .mobile_side_panel {
    display: block !important;
    position: sticky;
    top: 0;
    z-index: 1080;
  }
}

.mobile_side_panel {
  display: none;

  #nav-collapse {
    a {
      text-decoration: none;
    }
  }
  .navbar-brand {
    transition: all .3s ease;
    &:active {
      transform: scale(0.75,0.75);
    }
  }
}
