
#cart_calc_modal {

    // max-width: 660px;
    .myorange {
        background-color: #ff6700;
    }

    .mygray {
        background-color: #b0b0b0;
    }

    .modal-dialog {
        max-width: 660px ;

        .modal-header {
            font-size: 18px;
            color: #424242;
            background-color: #f5f5f5;
            position: relative;
            border: none !important;
            padding: 14px 20px;
            height: 32px;
            box-sizing: content-box;
            display: block;

            button {
                i.fa {
                    font-size: 24px;
                    color: #757575;
                }

                // position: absolute;
                background-color: transparent;
                // right:10px;
                // top:20px;
                display: block;
                border: none;
                float: right;
            }
        }

        .modal-body {
            padding: 20px;

            .item {
                position: relative;
                margin-bottom: 14px;

                &.input-active {
                    label {
                        top: -9px;
                        font-size: 12px;
                        background-color: #fff;
                    }
                }

                &.input-focus {
                    input[type="text"] {
                        border-color: #ff6700;
                    }

                    label {
                        color: #ff6700;
                    }
                }

                label {
                    position: absolute;
                    font-size: 14px;
                    color: #b0b0b0;
                    left: 12px;
                    top: 11px;
                    transition: all .2s linear;
                }

                input[type="text"] {
                    width: 100%;
                    border: solid 1px #e0e0e0;
                    background-color: #fff;
                    padding: 10px 16px;
                    font-size: 14px;
                    line-height: 18px;
                    outline: none;
                }
            }

        }

        .modal-footer {
            .footer_bts {
                width: 100%;
                display: flex;
                justify-content: space-between;
                button {
                    color: white;
                    width: 45%;
                    height: 40px;
                    font-size: 14px;
                    text-align: center;
                    border: none;
                    // margin: 0 7px;
                }

            }

        }
    }

}
